<template>
	<div class="three">
		<div class="operator">
			<button @click="handleControl('panLeft', -1)">左移</button>
			<button @click="handleControl('panLeft', 1)">右移</button>
			<button @click="handleControl('dollyIn', 1.1)">放大</button>
			<button @click="handleControl('dollyOut', 1.1)">
				缩小
			</button>
			<button @click="handleControl('rotateLeft', -0.1)">
				顺时针旋转
			</button>
			<button @click="handleControl('rotateLeft', 0.1)">
				逆时针旋转
			</button>
			<!-- <button @click="handleAutoRotate">自动旋转</button> -->
			<button @click="handleControl('reset')">复位</button>
		</div>
		<div class="stats-panel"></div>
		<div class="three-wrap"></div>
	</div>
</template>

<script>
import * as THREE from "three";
import "three-obj-mtl-loader";
import "three-orbitcontrols";
import * as TWEEN from "@tweenjs/tween.js";
import Stats from "stats.js";
import axios from "axios";

const COAL_COLOR = "#333";
const DL_COLOR = "#f40";
const SAVEFIX_PATH = ["/obj/SaveFix1.obj", "/obj/SaveFix2.obj"];
const DL_PATH = ["/obj/top.obj", "/obj/bottom.obj"];
const CARPATH = "/obj/car.obj";
const PERSONPATH = "/obj/person.obj";
// const CARCOLOR = "#efa804";
const CARCOLOR = "#f40";
const PERSONCOLOR = "#ef5104";
const REQUEST_INTEVAL = 16000;
let beforeData = "";
let beforeUwb = null;

export default {
	data() {
		return {
			scene: null,
			renderer: null,
			coal: null,
			uwb: {
				car: [],
				person: []
			}
		};
	},
	async mounted() {
		this.initStats();

		await this.loadScene();
		await this.loadDl();
		await this.loadSaveFix();
		const data = await this.getUwb();
		if (data) {
			this.uwb.person = data.person || [];
			this.uwb.car = data.cart || [];
		}
		if (this.uwb.person.length) {
			await this.loadPerson();
		}
		if (this.uwb.car.length) {
			await this.loadCar();
		}
		this.genTicks();
		this.render();
		this.move();
		this.uwbMove();
	},
	methods: {
		genTicks() {
			const total = 330;
			const interval = 10;
			const ticks = [...new Array(total / 10).keys()].map(
				v => (v + 1) * interval
			);

			const fontLoader = new THREE.FontLoader();
			fontLoader.load("fonts/helvetiker_regular.typeface.json", font => {
				for (let i = 0; i < ticks.length; i++) {
					const cube = new THREE.Mesh(
						new THREE.CubeGeometry(0.3, 0.01, 3),
						new THREE.MeshBasicMaterial({
							color: 0xff0000
						})
					);
					cube.position.set(ticks[i], 0, -6);

					const cube2 = new THREE.Mesh(
						new THREE.CubeGeometry(0.3, 0.01, 3),
						new THREE.MeshBasicMaterial({
							color: 0xff0000
						})
					);
					cube2.position.set(ticks[i], 0, 6);

					// add text
					const geometry = new THREE.TextGeometry(`${ticks[i]}`, {
						font,
						size: 1.5,
						height: 0.5,
						curveSegments: 2,
						bevelEnabled: false
					});
					geometry.center();
					geometry.rotateX(-Math.PI / 2);
					geometry.rotateY(Math.PI);
					const meshMaterial = new THREE.MeshLambertMaterial({
						color: 0xff0000
					});
					const mesh = new THREE.Mesh(geometry, meshMaterial);
					mesh.position.set(ticks[i], 0, -3);

					const geometry2 = new THREE.TextGeometry(`${ticks[i]}`, {
						font,
						size: 1.5,
						height: 0.5,
						curveSegments: 2,
						bevelEnabled: false
					});
					geometry2.center();
					geometry2.rotateX(-Math.PI / 2);
					const meshMaterial2 = new THREE.MeshLambertMaterial({
						color: 0xff0000
					});
					const mesh2 = new THREE.Mesh(geometry2, meshMaterial2);
					mesh2.position.set(ticks[i], 0, 3);
					this.scene.add(cube, cube2, mesh, mesh2);
				}
			});
			// cube.rotateY(-pos.angle);
		},
		getUwb() {
			return new Promise((resolve, reject) => {
				axios.post("/api/uwb").then(({ data }) => {
					if (data.code === 0) {
						resolve(data.data);
					} else {
						resolve(null);
					}
				});
			});
		},
		uwbMove() {
			this.timerUwb && clearInterval(this.timerUwb);
			this.timerUwb = setInterval(async () => {
				if (this.nowUwbData) beforeUwb = this.nowUwbData;
				const data = await this.getUwb();
				this.nowUwbData = data;

				const tweens = [];
				this.cars.length &&
					tweens.push(...this.genTween("cars", "cart", data));
				this.persons.length &&
					tweens.push(...this.genTween("persons", "person", data));

				const animate = () => {
					requestAnimationFrame(animate);
					tweens.map(tw => tw.update());
				};
				animate();
			}, REQUEST_INTEVAL);
		},
		genTween(modelName, datakey, data) {
			let before = null;
			if (beforeUwb) {
				before = beforeUwb;
			}
			const tweens = [];
			for (let i = 0; i < this[modelName].length; i++) {
				const [x, y, z] = before
					? before[datakey][i].split(" ").map(v => +v)
					: [0, 0, 0];
				const [x1, y1, z1] = data[datakey][i].split(" ").map(v => +v);
				tweens.push(
					new TWEEN.Tween({ x, y, z })
						.to({ x: x1, y: y1, z: z1 }, REQUEST_INTEVAL)
						.onUpdate(({ x, y, z }) => {
							this[modelName][i].position.x = x;
							this[modelName][i].position.y = z;
							this[modelName][i].position.z = y;
							this.render();
						})
						.start()
				);
			}
			return tweens;
		},
		move() {
			this.timer && clearInterval(this.timer);
			this.timer = setInterval(async () => {
				if (this.nowData) beforeData = this.nowData;
				const data = await this.getData();
				this.nowData = data;
				const [groupOneTween, groupTwoTween] = this.groupMove(data);
				const [oneHorRotateTween, twoHorRotateTween] = this.horRotate(
					data
				);
				const [oneVerRotateTween, twoVerRotateTween] = this.verRotate(
					data
				);

				const animate = () => {
					requestAnimationFrame(animate);
					groupOneTween.update();
					groupTwoTween.update();
					oneHorRotateTween.update();
					twoHorRotateTween.update();
					oneVerRotateTween.update();
					twoVerRotateTween.update();
				};
				animate();
			}, REQUEST_INTEVAL);
		},
		// 斗轮移动
		groupMove(target) {
			let beforeMove = [0, 0];
			const targetPos = target.split(",").map(v => +v.split(" ")[0]);
			if (beforeData) {
				beforeMove = beforeData.split(",").map(v => +v.split(" ")[0]);
			}

			return [
				new TWEEN.Tween({ x: beforeMove[0] })
					.to({ x: targetPos[0] }, REQUEST_INTEVAL)
					.onUpdate(({ x }) => {
						this.group.one.position.x = x;
						this.render();
					})
					.start(),

				new TWEEN.Tween({ x: beforeMove[1] })
					.to({ x: targetPos[1] }, REQUEST_INTEVAL)
					.onUpdate(({ x }) => {
						this.group.two.position.x = x;
						this.render();
					})
					.start()
			];
		},
		// 回转角度
		horRotate(target) {
			let beforeRotate = [0, 0];
			const targetPos = target.split(",").map(v => +v.split(" ")[1]);
			if (beforeData) {
				beforeRotate = beforeData.split(",").map(v => +v.split(" ")[1]);
			}

			return [
				new TWEEN.Tween({ y: beforeRotate[0] })
					.to({ y: targetPos[0] }, REQUEST_INTEVAL)
					.onUpdate(({ y }) => {
						this.group.one.children[0].rotation.y = this.degToAngle(
							y
						);
						this.render();
					})
					.delay(REQUEST_INTEVAL)
					.start(),
				new TWEEN.Tween({ y: beforeRotate[1] })
					.to({ y: targetPos[1] }, REQUEST_INTEVAL)
					.onUpdate(({ y }) => {
						this.group.two.children[0].rotation.y = this.degToAngle(
							y
						);
						this.render();
					})
					.delay(REQUEST_INTEVAL)
					.start()
			];
		},
		// 俯仰角度
		verRotate(target) {
			let beforeRotate = [0, 0];
			const targetPos = target.split(",").map(v => +v.split(" ")[2]);
			if (beforeData) {
				beforeRotate = beforeData.split(",").map(v => +v.split(" ")[2]);
			}

			return [
				new TWEEN.Tween({ z: beforeRotate[0] })
					.to({ z: targetPos[0] }, REQUEST_INTEVAL)
					.onUpdate(({ z }) => {
						this.group.one.children[0].rotation.z = this.degToAngle(
							z
						);
						this.render();
					})
					.delay(REQUEST_INTEVAL)
					.start(),
				new TWEEN.Tween({ z: beforeRotate[1] })
					.to({ z: targetPos[1] }, REQUEST_INTEVAL)
					.onUpdate(({ z }) => {
						this.group.two.children[0].rotation.z = this.degToAngle(
							z
						);
						this.render();
					})
					.delay(REQUEST_INTEVAL)
					.start()
			];
		},
		async getData() {
			return new Promise((resolve, reject) => {
				const g = this.getRangeData;
				const defaultData = `${g(20, 60)} ${g(80, 120)} ${g(
					10,
					30
				)},${g(120, 180)} ${g(-60, 60)} ${g(10, 23)}`;
				axios
					.post("/api/dw")
					.then(
						({ data }) => {
							if (data.code === 0) {
								resolve(data.data);
							} else {
								resolve(defaultData);
							}
						},
						_ => {
							resolve(defaultData);
						}
					)
					.catch(_ => resolve(defaultData));
			});
		},
		async loadSaveFix() {
			const ps = SAVEFIX_PATH.map(path => this.loadObj(path, COAL_COLOR));
			this.saveFixs = await Promise.all(ps);
			this.scene.add(...this.saveFixs);
			console.log("savefix loaded...");
		},
		async loadDl() {
			const ps = DL_PATH.map(path => this.loadObj(path, DL_COLOR));
			this.dl = await Promise.all(ps);
			this.dl = this.dl.concat(this.dl.map(model => model.clone()));
			this.dl.map(
				(model, index) =>
					(model.name = (index + 1) % 2 ? "top" : "bottom")
			);
			this.group = {
				one: new THREE.Group(),
				two: new THREE.Group()
			};
			this.group.one.add(this.dl[0], this.dl[1]);
			this.group.two.add(this.dl[2], this.dl[3]);
			// 移动到煤堆尾部并旋转
			// this.group.two.position.x = 320;
			// this.group.two.rotation.y = Math.PI;
			this.scene.add(this.group.one, this.group.two);
			console.log("dl loaded...");
		},
		async loadCar() {
			const car = await this.loadObj(CARPATH, CARCOLOR);
			car.scale.set(0.002, 0.002, 0.002);
			this.cars = [];
			for (let i = 0; i < this.uwb.car.length; i++) {
				const [x, y, z] = this.uwb.car[i].split(" ").map(v => +v);
				const temp = car.clone();
				// z->y  y->z
				temp.position.x = x;
				temp.position.y = z;
				temp.position.z = y;
				this.cars.push(temp);
			}
			this.scene.add(...this.cars);
			console.log("car loaded...");
		},
		async loadPerson() {
			const person = await this.loadObj(PERSONPATH, PERSONCOLOR);
			person.scale.set(0.03, 0.03, 0.03);
			this.persons = [];
			for (let i = 0; i < this.uwb.person.length; i++) {
				const [x, y, z] = this.uwb.person[i].split(" ").map(v => +v);
				const temp = person.clone();
				temp.position.x = x;
				temp.position.y = z;
				temp.position.z = y;
				this.persons.push(temp);
			}
			this.scene.add(...this.persons);
			console.log("person loaded...");
		},
		async loadScene() {
			return new Promise((resolve, reject) => {
				const dom = document.querySelector(".three-wrap");
				this.scene = new THREE.Scene();

				// 点光源
				var point = new THREE.PointLight(0xffffff, 1.5);
				point.position.set(100, 100, 60);
				point.castShadow = true;
				this.scene.add(point);

				// 环境光
				var ambient = new THREE.AmbientLight(0xcccccc, 0.6);
				this.scene.add(ambient);

				var width = dom.offsetWidth;
				var height = dom.offsetHeight;

				this.camera = new THREE.PerspectiveCamera(
					49,
					width / height,
					1,
					1200
				);
				this.camera.zoom = 1;
				this.camera.position.set(430, 135, 217);
				this.camera.rotation.set(-30, 48, 23);
				this.camera.lookAt(this.scene.position);
				this.camera.updateProjectionMatrix();

				this.renderer = new THREE.WebGLRenderer();
				this.renderer.shadowMap.enabled = true;
				this.renderer.setSize(width, height);
				this.renderer.setClearColor("#ccc", 0.8); // 设置背景颜色
				dom.appendChild(this.renderer.domElement);

				// init control
				this.controls = new THREE.OrbitControls(
					this.camera,
					this.renderer.domElement
				);
				this.controls.target = new THREE.Vector3(203, 0, 0);
				this.controls.addEventListener("change", this.render);
				this.controls.update();
				this.controls.saveState();
				this.addResize();
				resolve();
			});
		},
		// 加载模型
		async loadObj(path, color) {
			var loader = new THREE.OBJLoader();
			return new Promise((resolve, reject) => {
				loader.load(
					path,
					model => {
						Object.assign(model, {
							castShadow: true,
							reciveShadow: true
						});

						model.children.forEach(v =>
							v.material.color.set(color)
						);

						resolve(model);
					},
					null,
					_ => reject(new Error("obj load error"))
				);
			});
		},
		render() {
			this.renderer.render(this.scene, this.camera); // 执行渲染操作
		},
		initStats() {
			this.statsList = [];
			for (let i = 0; i < 3; i++) {
				if (i == 1) continue;
				const stats = new Stats();
				stats.domElement.style.position = "static";
				stats.showPanel(i);
				this.statsList.push(stats);
				document.querySelector(".stats-panel").appendChild(stats.dom);
			}

			const animate = () => {
				this.statsList.map(stats => {
					stats.begin();
					stats.end();
				});
				requestAnimationFrame(animate);
			};

			requestAnimationFrame(animate);
		},
		// 添加window的resize事件
		addResize() {
			window.addEventListener("resize", this.resize, false);
		},
		// 移除window的resize事件
		removeResize() {
			window.removeEventListener("resize", this.resize, false);
		},
		// window resize
		resize(event) {
			const dom = document.querySelector(".three-wrap");
			const SCREEN_WIDTH = dom.offsetWidth;
			const SCREEN_HEIGHT = dom.offsetHeight;
			this.renderer.setSize(SCREEN_WIDTH, SCREEN_HEIGHT);
			// this.camera.aspect = SCREEN_WIDTH / SCREEN_HEIGHT
			this.camera.updateProjectionMatrix();
		},
		handleAutoRotate() {
			this.autoRotateAnimate();
		},
		// 自动旋转
		autoRotateAnimate() {
			requestAnimationFrame(this.autoRotateAnimate);
			this.controls.rotateLeft(-0.005);
			this.controls.update();
			this.renderer.render(this.scene, this.camera);
		},
		handleControl(type, num) {
			if (type === "panLeft") {
				const matrix = this.controls.object.matrix;
				this.controls[type](num, matrix);
			} else {
				this.controls[type](num);
			}

			this.controls.update();
			this.render();
		},
		// 角度转弧度
		degToAngle(deg) {
			return (deg / 180) * Math.PI;
		},
		// 弧度转角度
		angleToDeg(angle) {
			return (angle / Math.PI) * 180;
		},
		// 旋转物体
		handleRotate(objName, deg, axis) {
			deg = +deg;
			if (objName === "belt" && this.belt) {
				const { x, y, z } = this.belt.position;
				this.belt.position.set(0, 0, 0);
				this.belt.rotation[axis] = this.degToAngle(deg);
				this.belt.position.set(x, y, z);
				this.render();
			} else {
				if (this[objName]) {
					this[objName].rotation[axis] =
						this.degToAngle(deg) % (2 * Math.PI);
					this.render();
				}
			}
		},
		getRangeData(n, m) {
			return parseInt(Math.random() * (m - n + 1) + n);
		},
		// 删除obj
		deleteItem(group) {
			try {
				this.scene.remove(group);
			} catch (error) {}
			if (!group) return;
			group.traverse(function(item) {
				if (item instanceof THREE.Mesh) {
					item.geometry.dispose();
					item.material.dispose();
				}
			});
		}
	}
};
</script>

<style lang="less">
.three {
	width: 100%;
	height: 100%;
	background: #ccc;
	position: relative;
	.operator {
		position: absolute;
		left: 0;
		top: 0;
		button {
			cursor: pointer;
			padding: 2px 4px;
			margin: 10px;
		}
	}
	.stats-panel {
		position: absolute;
		width: 100%;
		height: 50px;
		bottom: 0;
		right: 0;
		display: flex;
		flex-direction: row;
		justify-content: flex-end;
		& > div {
			margin-left: 20px;
		}
	}

	.three-wrap {
		width: 100%;
		height: 100%;
	}
}
</style>
